import React, { useState, useMemo } from 'react';
import PropTypes from 'prop-types';
import {
  aggregatedClasses,
  convertPropToCssClassName,
} from '@elliemae/ds-classnames';
import { cx } from '@elliemae/ds-utilities/utils';
import {
  BUTTON_TYPE,
  sizes,
  buttonTypes,
  buttonVariants,
  BUTTON_SIZE,
  BUTTON_VARIANT,
} from '../utils/prop-types';

const blockName = 'button';

const getContainer = Element =>
  aggregatedClasses(Element)(blockName, null, ({ iconOnly, variant }) => ({
    'icon-only': iconOnly,
    [variant]: variant,
  }));

const DSButton = ({
  innerRef,
  disabled = false,
  buttonType = BUTTON_TYPE.PRIMARY,
  fluidWidth = false,
  size = BUTTON_SIZE.M,
  labelText = '',
  icon = null,
  onBlur = () => null,
  onClick = () => null,
  onKeyPress = () => null,
  leftIcon = null,
  tabIndex = 0,
  variant = BUTTON_VARIANT.DEFAULT,
  intent,
  containerProps = {},
  className,
  as: Component = 'button',
  type = 'button',
  ...rest
}) => {
  const [pressed, setPressed] = useState(false);
  const Button = useMemo(() => getContainer(Component), [Component]);
  const intentBlockName = `${blockName}${intent ? `-${intent}` : ''}`;
  const { cssClassName: intentClassName } = convertPropToCssClassName(
    intentBlockName,
  );
  return (
    <Button
      data-testid={containerProps['data-testid'] || 'button-wrapper'}
      {...rest}
      {...containerProps}
      aria-disabled={disabled}
      aria-label={rest['aria-label'] || labelText || 'button'}
      aria-pressed={pressed}
      className={cx(intentClassName, className)}
      classProps={{
        disabled,
        buttonType,
        size,
        fluidWidth,
        iconOnly: !labelText && (icon || leftIcon),
        variant: disabled ? BUTTON_VARIANT.DISABLED : variant,
      }}
      disabled={disabled} // https://jira.elliemae.io/browse/PUI-1215
      innerRef={innerRef}
      onBlur={e => {
        if (disabled) return;
        setPressed(false);
        onBlur(e);
      }}
      onClick={e => {
        if (disabled) return;
        setPressed(true);
        onClick(e);
      }}
      onKeyPress={e => {
        if (disabled) return;
        e.preventDefault();
        if (e.key === 'Enter') {
          setPressed(true);
          onClick(e);
        }
        onKeyPress(e);
      }}
      tabIndex={tabIndex}
      type={type}
    >
      {leftIcon}
      {labelText && (
        <span data-testid="button-label" role="presentation">
          {labelText}
        </span>
      )}
      {icon}
    </Button>
  );
};

DSButton.propTypes = {
  /**
   * Whether the button is disabled or not
   */
  disabled: PropTypes.bool,
  /**
   * Make the content of the button fit the button container or not
   */
  fluidWidth: PropTypes.bool,
  /**
   * Displayable label inside the button
   */
  labelText: PropTypes.oneOfType([PropTypes.string, PropTypes.element]),
  /**
   * Put an icon inside the button
   */
  icon: PropTypes.element,
  /**
   * Allows a function that is triggered once the button is clicked
   */
  onClick: PropTypes.func,
  /**
   * Allows a function that is triggered once the button is key pressed
   */
  onKeyPress: PropTypes.func,
  /**
   * Put an icon on the left side of the button
   */
  leftIcon: PropTypes.element,
  /**
   * ['primary', 'secondary', 'text', 'link']
   */
  buttonType: PropTypes.oneOf(buttonTypes),
  /**
   * ['s', 'm', 'l']
   */
  size: PropTypes.oneOf(sizes),
  /**
   * [
   *  'variant-default',
   *  'variant-focus',
   *  'variant-active',
   *  'variant-disabled',
   * ]
   */
  variant: PropTypes.oneOf(buttonVariants),
};

export {
  BUTTON_TYPE,
  BUTTON_SIZE,
  BUTTON_VARIANT,
  buttonVariants,
  buttonTypes,
};
export default DSButton;
