import React, { useMemo, useRef, useState } from 'react';
import FocusGroupProvider from '@elliemae/ds-shared/FocusGroup/FocusGroupManager';
import useFocusGroupItem from '@elliemae/ds-shared/FocusGroup/useFocusGroupItem';
import { mergeRefs } from '@elliemae/ds-utilities/system';
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin';

// eslint-disable-next-line react/display-name
const TreeListRovingHoc = instance => props => {
  const { hotKeys } = instance.getInstance();
  return (
    <FocusGroupProvider hotKeys={hotKeys}>{props.children}</FocusGroupProvider>
  );
};

// eslint-disable-next-line react/display-name
const TreeItemRovingHoc = Component => ({
  innerRef,
  onFocus,
  onBlur,
  ...props
}) => {
  const ref = useRef(null);
  useFocusGroupItem(ref, {
    onFocus,
    onBlur,
  });
  return <Component {...props} innerRef={mergeRefs(ref, innerRef)} />;
};

export const TreeRovingPlugin = createInstancePlugin('tree-data', {
  getItemProps(props) {
    const [isItemFocused, setIsItemFocused] = useState(false);
    return {
      ...props,
      onBlur: () => setIsItemFocused(false),
      onFocus: () => setIsItemFocused(true),
      isItemFocused,
      'data-index': props.index,
    };
  },
  renderers(renderers, instance) {
    const ListWrapper = useMemo(() => TreeListRovingHoc(instance), []);
    const Item = useMemo(() => TreeItemRovingHoc(renderers.item, instance), []);

    return {
      ...renderers,
      listWrapper: ListWrapper,
      item: Item,
    };
  },
});

TreeRovingPlugin.order = 0;
