import React, { useMemo, useRef } from 'react';
import { DynamicSizeList } from '@john-osullivan/react-window-dynamic-fork';
import { getVirtualListComponent } from '@elliemae/ds-shared/virtualization';
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin';
import { TreeListNoItems } from '../../renderers';

const VirtualizedTreeItem = React.forwardRef(({ index, style, data }, ref) => {
  const { instance, records } = data;
  const { itemRenderer, getItemProps, renderers } = instance.getInstance();
  const record = records[index];

  const ItemComponent = renderers.item;

  // needed to show the shadow below the row and because
  // of absolute positioning from react-window
  const mixedProps = getItemProps(
    {
      index,
      record,
      style,
      innerRef: ref,
    },
    { record },
  );

  record.style = style;

  instance.positionedRecords.push(record);

  return (
    <ItemComponent {...mixedProps} tabIndex={-1}>
      {itemRenderer(mixedProps)}
    </ItemComponent>
  );
});

// eslint-disable-next-line react/display-name
const VirtualizedTree = (renderers, instance) => ({
  records,
  itemRenderer,
  innerRef,
  ...rest
}) => {
  const listRef = useRef();
  const {
    props: {
      autoHeight,
      fluid,
      width,
      height = 500,
      noItemsPlaceholder = 'No items found',
      initialOffset,
    },
    renderers: currRenderers,
  } = instance.getInstance();

  const ListComponent = useMemo(
    () =>
      getVirtualListComponent({
        component: DynamicSizeList,
        height,
        fluid,
        autoHeight,
      }),
    [height, autoHeight],
  );

  const itemData = useMemo(
    () => ({
      instance,
      renderers: currRenderers,
      records,
    }),
    [records, currRenderers],
  ); // todo: revise this

  if (records && !records.length) {
    return <TreeListNoItems>{noItemsPlaceholder}</TreeListNoItems>;
  }

  return (
    <ListComponent
      {...rest}
      ref={listRef}
      component={DynamicSizeList}
      height={height}
      initialScrollOffset={initialOffset}
      itemCount={records.length}
      itemData={itemData}
      outerElementType={renderers.list}
      outerRef={innerRef}
      width={width}
    >
      {VirtualizedTreeItem}
    </ListComponent>
  );
};

export const TreeVirtualizationPlugin = createInstancePlugin(
  'tree-virtualization',
  {
    renderers(renderers, instance) {
      return {
        item: renderers.item,
        list: useMemo(() => VirtualizedTree(renderers, instance), []),
      };
    },
  },
);
