import { aggregatedClasses } from '@elliemae/ds-classnames';
import React from 'react';

export const treeListBlockName = 'tree-list';
export const treeItemBlockName = 'tree-item';
export const treeListNoItemsBn = `${treeListBlockName}-no-items`;

export const TreeListItem = aggregatedClasses('li', {
  'data-testid': treeItemBlockName,
  role: 'treeitem',
})(treeItemBlockName);
export const TreeListItemAddonsWrapper = aggregatedClasses('div')(
  treeItemBlockName,
  'addons',
);
export const TreeList = aggregatedClasses('ul', {
  'data-testid': treeListBlockName,
  role: 'tree',
})(treeListBlockName, null, ({ rowSize }) => ({
  [`rowsize-${rowSize}`]: rowSize,
}));
export const TreeListNoItems = aggregatedClasses('div', {
  'data-testid': treeListNoItemsBn,
})(treeListNoItemsBn);

function TreeItem({ leftAddons, rightAddons, children, ...rest }) {
  return (
    <TreeListItem {...rest}>
      {leftAddons && (
        <TreeListItemAddonsWrapper>{leftAddons}</TreeListItemAddonsWrapper>
      )}
      {children}
      {rightAddons && (
        <TreeListItemAddonsWrapper>{rightAddons}</TreeListItemAddonsWrapper>
      )}
    </TreeListItem>
  );
}

export const TreeRenderers = {
  list: TreeList,
  item: TreeItem,
};
