/* eslint max-lines: ["error", 300] */
import React, { useCallback } from 'react';
import { cx, isFunction } from '@elliemae/ds-utilities/utils';
import Button from '@elliemae/ds-basic/Button';
import ArrowheadDown from '@elliemae/ds-icons/ArrowheadDown';
import ArrowheadRight from '@elliemae/ds-icons/ArrowheadRight';
import { aggregatedClasses } from '@elliemae/ds-classnames';
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin';
import { walkTreeStrategy } from './walkTreeStrategy';
import {
  getItemChildrenLength,
  getParentId,
  findItemOnDocument,
  getListFromDocument,
} from './utils';
import { treeItemBlockName } from '../../renderers';
import { useExpandTreeState } from './useExpandTreeState';

const ItemContent = aggregatedClasses('div')(`${treeItemBlockName}-content`);
const ItemValue = aggregatedClasses('span')(treeItemBlockName, 'value');

const noop = () => null;

const getItemNestingLeftPadding = (level, opts) => {
  const { spaceForNestingLevel, isChild } = {
    spaceForNestingLevel: 1.5,
    ...opts,
  };
  const groupLeftPadding = spaceForNestingLevel * (level - 1);
  return isChild
    ? `${groupLeftPadding + spaceForNestingLevel - 0.1}em`
    : `${groupLeftPadding}em`;
};
const itemRenderer = (value, itemProps, instance) => {
  const { record } = itemProps;
  const {
    props: { showChildrenAmount = false, labelRenderer, onItemClick = noop },
  } = instance.getInstance();

  // eslint-disable-next-line react-hooks/rules-of-hooks
  const handleItemClick = useCallback(
    (e) => {
      onItemClick(record.data, e);
    },
    [record.data, onItemClick],
  );

  const amountChildren = Number(getItemChildrenLength(record));

  const renderedLabel =
    (isFunction(labelRenderer) && labelRenderer(value, itemProps)) || value;

  return (
    <ItemContent
      data-testid={`${treeItemBlockName}-content`}
      onClick={handleItemClick}
    >
      <ItemValue>
        {renderedLabel}
        {showChildrenAmount && record.isGroup && ` (${amountChildren})`}
      </ItemValue>
    </ItemContent>
  );
};
itemRenderer.order = 100;

export const TreePluginList = createInstancePlugin('tree-data', {
  state: useExpandTreeState,

  recordIterator: (opts) => {
    const shouldWalkChildren = (record) => {
      const {
        state: { expandedAll },
        decorators,
      } = opts.instance.getInstance();

      const shouldWalk = decorators.shouldWalkChildren.some((decorator) =>
        decorator(record, opts.instance),
      );
      if (shouldWalk) return true;
      if (expandedAll === true) return true;
      return !record.parentNode.id || record.parentNode.expanded;
    };
    walkTreeStrategy({ ...opts, shouldWalkChildren });
  },

  getItemProps(props, instance, { record }) {
    const { nestingLevel } = record;
    const {
      actions: { _toggleExpand },
      props: { itemIcon, groupIcon, spaceForNestingLevel = 1.5 },
    } = instance.getInstance();

    // eslint-disable-next-line react-hooks/rules-of-hooks
    const handleExpandRow = useCallback(() => _toggleExpand(record.data), [
      record.data,
    ]);
    const icon = record.isGroup ? groupIcon : itemIcon;

    const itemNestingLeftPadding = getItemNestingLeftPadding(
      nestingLevel || record.nestingLevel,
      {
        isChild: !record.isGroup,
        spaceForNestingLevel,
      },
    );

    const expandAddon = record.isGroup && (
      <Button
        buttonType="text"
        className="expandable-arrow"
        data-testid="tree-item-expand-toggle"
        icon={
          record.expanded ? (
            <ArrowheadDown data-testid="ic-arrow-head-down" />
          ) : (
            <ArrowheadRight data-testid="ic-arrow-head-right" />
          )
        }
        onClick={handleExpandRow}
        size="s"
        tabIndex={-1}
        // eslint-disable-next-line max-lines
      />
    );

    const iconAddon =
      icon &&
      React.cloneElement(icon, {
        'data-testid': record.isGroup
          ? 'tree-item-group-icon'
          : 'tree-item-icon',
        className: cx(icon.props.className, 'tree-item-icon'),
      });

    const spaceAddon = (
      <div
        data-testid="tree-item-nested-space"
        style={{ width: itemNestingLeftPadding }}
      />
    );

    return {
      ...props,
      'data-id': record.id,
      'data-parentid': getParentId(record),
      'aria-expanded': !!record.expanded,
      leftAddons: [
        ...(props.leftAddons || []),
        spaceAddon,
        expandAddon,
        iconAddon,
      ],
    };
  },

  record: (record, instance) => {
    const {
      state: { expanded, expandedAll },
    } = instance.getInstance();

    const isParentExpanded =
      !record.parentNode.id || record.parentNode.expanded;

    // eslint-disable-next-line no-param-reassign
    if (expandedAll === true) record.expanded = true;
    // eslint-disable-next-line no-param-reassign
    else if (expandedAll === false) record.expanded = false;
    // eslint-disable-next-line no-param-reassign
    else record.expanded = isParentExpanded && !!expanded[record.id];

    return isParentExpanded && record;
  },

  hotKeys(instance) {
    function handleExpand({ index }) {
      const {
        records,
        actions: { _toggleExpand },
      } = instance.getInstance();
      const record = records[index];
      if (record && record.data.children.length > 0) _toggleExpand(record.data);
    }

    function handleCollapse({ index }) {
      const {
        records,
        actions: { collapse },
      } = instance.getInstance();
      const record = records[index];
      if (record && record.expanded) {
        collapse(record);
      }
    }

    function handleExpandAll() {
      const {
        actions: { toggleExpandAll },
      } = instance.getInstance();
      const active = document.activeElement;

      toggleExpandAll().then(() => {
        const { records } = instance.getInstance();
        const itemToFocus = records.find(
          // eslint-disable-next-line eqeqeq
          (record) => record.id == active.dataset.id,
        );
        if (itemToFocus) {
          // eslint-disable-next-line no-unused-expressions
          findItemOnDocument(itemToFocus.id)
            ? findItemOnDocument(itemToFocus.id).focus()
            : findItemOnDocument(records[0].id).focus();
        } else {
          getListFromDocument().scrollTo(0, 0);
          setTimeout(() => {
            findItemOnDocument(records[0].id).focus();
          }, 200);
        }
      });
    }

    return [
      {
        key: 'right',
        handler: handleExpand,
      },
      {
        key: 'shift+=',
        handler: handleExpand,
      },
      {
        key: '-',
        handler: handleCollapse,
      },
      {
        key: 'enter',
        handler: ({ index }) => {
          const {
            records,
            actions: { select },
          } = instance.getInstance();
          const record = records[index];
          select(record);
        },
      },
      {
        key: 'left',
        handler: handleCollapse,
      },
      {
        key: '/',
        handler: handleCollapse,
      },
      {
        key: 'shift+8',
        allowDocumentHandler: true,
        handler: handleExpandAll,
      },
    ];
  },
  itemRenderer,
});
