import React from 'react';
import useDataList from '@elliemae/ds-shared/useDataList';
import { TreeRenderers } from './renderers';
import { TreePluginList } from './plugins/tree';
import { TreeVirtualizationPlugin } from './plugins/virtualization/TreeVirtualizationPlugin';
import { TreeRovingPlugin } from './plugins/roving';

const wrapComponentIfExists = (Wrapper, children) =>
  Wrapper ? <Wrapper>{children}</Wrapper> : children;

export function TreeView({
  data,
  plugins = [],
  children,
  roving = true,
  rowSize = 'normal',
  ...props
}) {
  const instance = useDataList({
    data,
    plugins: [
      TreePluginList,
      TreeVirtualizationPlugin,
      roving && TreeRovingPlugin,
      ...plugins,
    ],
    customDecorators: { shouldWalkChildren: [] }, // adds a new decorator to the useDataList specific for trees
    renderers: TreeRenderers,
    ...props,
    rowSize,
  });

  const { list: ListComponent, listWrapper: Wrapper } = instance.renderers;
  const { getListProps } = instance;

  return wrapComponentIfExists(
    Wrapper,
    <ListComponent
      records={instance.records}
      {...getListProps()}
      className={`em-ds-tv-row-size-${rowSize}`}
    />,
  );
}
