/* eslint no-param-reassign: [2, { "props": false }] */
/* eslint max-lines: ["error", 300] */
import React, { useCallback, useMemo } from 'react';
import Checkbox from '@elliemae/ds-basic/form/Checkbox';
import Radio from '@elliemae/ds-basic/form/Radio';
import { cx, isFunction } from '@elliemae/ds-utilities/utils';
import useDerivedStateFromProps from '@elliemae/ds-utilities/hooks/useDerivedStateFromProps';
import createInstancePlugin from '@elliemae/ds-shared/createDataInstance/createInstancePlugin';
import { walkTreeStrategy } from '../tree/walkTreeStrategy';
import { getParentChildren, getParentId } from '../tree/utils';
import { checkParentState } from './utils';

const updateParentState = ({
  node,
  checked,
  selectionState,
  isCurrentParentChildren = true,
}) => {
  const { parentNode } = node;

  if (parentNode) {
    let nextSelectedChildrenAmount;
    // increase or decrease the amount
    if (checked === true && isCurrentParentChildren)
      nextSelectedChildrenAmount = parentNode.selectedChildren.length;
    if (checked === true && !isCurrentParentChildren)
      nextSelectedChildrenAmount = parentNode.selectedChildren.length + 1;
    if (checked === false)
      nextSelectedChildrenAmount = parentNode.selectedChildren.length - 1;

    const parentChildren = getParentChildren(node);
    const parentChildrenLength = parentChildren && parentChildren.length;

    // check parent state
    const parentState = checkParentState(
      nextSelectedChildrenAmount,
      parentChildrenLength,
      checked,
    );

    selectionState[parentNode.id || 'all'] = parentState;

    updateParentState({
      node: parentNode,
      checked: parentState,
      selectionState,
      isCurrentParentChildren: false,
    });
  }
};

const useSelectableTreeState = (instance) => {
  const {
    originalData,
    idKey = 'id',
    props: {
      onSelect = () => null,
      selection: selectionProp = {},
      onSelectionChange = () => null,
      isMultiSelect = false,
      isSingleSelect = false,
    },
  } = instance;
  const [selection, setSelection] = useDerivedStateFromProps(selectionProp);

  const isAllSelected = useMemo(() => {
    if (!isMultiSelect) return false;
    const amountSelected = originalData.reduce(
      (result, item) => (selection[item[idKey]] === true ? result + 1 : result),
      0,
    );

    if (amountSelected === originalData.length) {
      return true;
    }
    if (amountSelected === 0) {
      return false;
    }
    return 'mixed';
  }, [selection]);

  const select = useCallback((record) => {
    if (isFunction(onSelect)) onSelect(record.data);
    setSelection((prevSelection) => {
      const nextSelectionState = isMultiSelect ? { ...prevSelection } : {};
      const nextState = isMultiSelect ? !prevSelection[record.id] : {};

      nextSelectionState[record.id] = nextState;

      if (isMultiSelect || isSingleSelect) {
        // if node group then select all its leafs
        if (record.isGroup) {
          if (record.parentNode && nextState)
            record.parentNode.selectedChildren.push(record.id);
          if (!nextState) record.selectedChildren = [];
          // get all children
          walkTreeStrategy({
            data: record.data.children,
            parentNode: record,
            cb: (node) => {
              const parentId = getParentId(node);
              // if direct child and truthy next state
              if (nextState && parentId === record.id) {
                record.selectedChildren.push(node.id);
              }
              nextSelectionState[node.id] = nextState;
            },
          });
        }
        // mutates the cloned selection state
        updateParentState({
          node: record,
          checked: nextState,
          selectionState: nextSelectionState,
        });
      }

      onSelectionChange(nextSelectionState, record.data);
      return nextSelectionState;
    });
  }, []);

  return {
    state: { selection, isAllSelected },
    actions: { select },
  };
};

const updateParentInternalState = (record, checked) => {
  const { parentNode } = record;
  if (!parentNode) return;

  // if next state is checked
  if (checked === true) {
    parentNode.selectedChildren.push(record.id);
  } else {
    parentNode.selectedChildren = parentNode.selectedChildren.filter(
      (id) => id !== record.id,
    );
  }
};

const SelectionFormatter = (value, { record }, instance) => {
  const {
    actions: { select },
    props: { isMultiSelect },
  } = instance.getInstance();
  const handleSelect = () => select(record);

  return (
    <>
      {isMultiSelect && (
        <Checkbox
          checked={record.checked}
          className="tree-item-checkbox"
          data-testid="tree-item-checkbox"
          onChange={handleSelect}
          tabIndex={-1}
        />
      )}
      {value}
    </>
  );
};
SelectionFormatter.order = 300;

export const SelectablePluginTree = createInstancePlugin('selectable', {
  state: useSelectableTreeState,
  // itemRenderer: SelectionFormatter,
  record: (record, instance) => {
    const {
      state: { selection },
      props: { isMultiSelect, isSingleSelect },
    } = instance.getInstance();

    record.checked = selection[record.id];

    if (isMultiSelect || isSingleSelect) {
      // if it's the root of the tree declare the selectedChildren array
      if (
        !record.parentNode.id &&
        record.parentNode.selectedChildren === undefined
      ) {
        record.parentNode.selectedChildren = [];
      }
      if (record.isGroup && record.selectedChildren === undefined) {
        record.selectedChildren = [];
      }
      updateParentInternalState(record, record.checked);
    }

    return record;
  },
  getItemProps(props, instance, { record }) {
    const {
      actions: { select },
      props: { isMultiSelect, isSingleSelect, disableLeafs, disableGroups },
    } = instance.getInstance();
    const handleSelect = () => select(record);

    const checkboxAddon = isMultiSelect && (
      <Checkbox
        checked={record.checked}
        disabled={record.isGroup ? disableGroups : disableLeafs}
        className="tree-item-checkbox"
        data-testid="tree-item-checkbox"
        onChange={handleSelect}
        tabIndex={-1}
      />
    );

    const radioAddon = isSingleSelect && !isMultiSelect && (
      <Radio
        checked={record.checked}
        disabled={record.isGroup ? disableGroups : disableLeafs}
        className="tree-item-radio"
        data-testid="tree-item-radio"
        onChange={handleSelect}
        tabIndex={-1}
      />
    );

    if (isMultiSelect || isSingleSelect) {
      return {
        ...props,
        leftAddons: [checkboxAddon, radioAddon, ...(props.leftAddons || [])],
        'aria-selected': !!record.checked,
      };
    }

    return {
      ...props,
      onClick: handleSelect,
      className: cx(props.className, record.checked && 'selected'),
      'aria-selected': !!record.checked,
    };
  },

  hotKeys(grid) {
    return {
      key: 'space',
      handler: ({ index }) => {
        const {
          records,
          actions: { select },
        } = grid.getInstance();
        select(records[index]);
      },
    };
  },
});
